.root {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 15px;

  :global {
    li {
      a {
        color: #fe5f1e;
      }
      cursor: pointer;
      width: 25px;
      height: 25px;
      // background: #fe5f1e;
      border-radius: 50%;
      border: 1px solid #fe5f1e;
      display: flex;
      justify-content: center;
      align-items: center;

      &:hover {
        a {
          color: white;
        }

        background: #fe5f1e;
      }
    }

    .selected {
      a {
        color: white;
      }

      background: #fe5f1e;
    }
  }
}
