.blockSearch {
    // display: flex;
    // align-items: center;
    position: relative;
    
    input {
        border: 1px solid rgba(0, 0, 0, 0.107);
        border-radius: 6px;
        width: 270px;
        height: 40px;
        font-size: 20px;
        padding-left: 30px;
        padding-bottom: 4px;
        transition: .5s;
        cursor: pointer;
        // &:focus {
        //     width: 270px;
        //     border: 1px solid rgba(0, 0, 0, 0.2);
        //     transition: .5s;
        // }
    }

}

.searchIco {
    width: 20px;
    height: 20px;
    margin-right: 8px;
    position: absolute;
    opacity: 0.3;
    top: 9px;
    margin-left: 5px;
    cursor: pointer;
}


.closeIco {
    position: absolute;
    left: 250px;
    width: 12px;
    height: 12px;
    top: 15px;
    opacity: 0.3;
    &:hover {
        opacity: 0.8;
    }
}